height: 100vh;\n  position: relative;\n\n  background-image: url('./assets/images/background.png');\n  overflow: hidden;\n\n  @include flex-container;\n\n  .home-btn,\n  .sound-btn {\n    @include button(32px, 32px, 80%, #00000011, 70%);\n\n    position: absolute;\n    top: 8px;\n\n    border: 0;\n    outline: 0;\n    filter: invert(1);\n  }\n\n  .home-btn {\n    left: 8px;\n  }\n\n  .sound-btn {\n    right: 8px;\n\n    &__icon {\n      display: none;\n    }\n\n    &__icon--active {\n      display: block;\n    }\n  }\n}\n\n.home {\n  @include flex-container(column, center, center, 8px);\n\n  height: 500px;\n  width: 700px;\n\n  padding: 36px 24px;\n\n  @include box-shadow(#00000033);\n  background: rgb(56,134,199);\n  background: radial-gradient(circle, #3886c7cc 0%, #215b8bcc 100%);\n  border-radius: 24px;\n\n  .logo {\n    @include flex-container(row, center, center, 12px);\n\n    filter: invert(1);\n    opacity: 0.9;\n    user-select: none;\n\n    &__icon {\n      opacity: 0.85;\n\n      &--1 {\n        height: 72px;\n      }\n      \n      &--2 {\n        height: 56px;\n      }\n    }\n\n    &__title {\n      font-family: \"Old Stamper\", \"Josefin Sans\", sans-serif;\n      font-size: 48px;\n    }\n  }\n\n  .players {\n    @include flex-container(row, center, center, 16px);\n\n    &__vs {\n      opacity: 90%;\n      color: white;\n      font-size: 64px;\n      font-family: 'D Day Stencil', \"Josefin Sans\", sans-serif;\n      user-select: none;\n    }\n\n    .player {\n      @include flex-container(column, center, center, 8px);\n      @include box-shadow(#00000077, 0, 0, 2px, 0);\n\n      padding: 32px;\n      border-radius: 16px;\n\n      background: radial-gradient(circle, #8eb7f577 0%, #6c91c777 150%);\n\n      &__avatar {\n        height: 168px;\n        border-radius: 50%;\n\n        @include box-shadow(#000000ff, 0, 0, 2px);\n\n        &--human {\n          display: none;\n        }\n      }\n\n      &__name {\n        max-width: 150px;\n        border: none;\n        outline: none;\n        \n        font-size: 24px;\n        text-wrap: nowrap;\n        overflow: hidden;\n\n        margin-bottom: 8px;\n      }\n\n      &__options {\n        @include flex-container(row, center, center, 12px);\n\n        input[type=radio] {\n          display: none;\n        }\n\n        input[type=radio]:checked + label {\n          background-color: #202074;\n          opacity: 90%;\n        }\n\n        label {\n          @include button(min-content, 36px, 80%, #202074cc);\n\n          padding: 4px 8px;\n          border-radius: 8px;\n\n          opacity: 70%;\n          border: 2px #202074cc solid;\n\n          color: white;\n          font-size: 0.9rem;\n        }\n      }\n    }\n  }\n\n  .play-btn {\n    @include button(min-content, 36px, 80%, #202074);\n\n    padding: 8px 12px;\n    border-radius: 8px;\n\n    opacity: 90%;\n    background-color: #202074cc;\n    border: none;\n    outline: none;\n\n    color: white;\n    font-size: 1.8rem;\n    font-family: 'D Day Stencil', \"Josefin Sans\", sans-serif;\n  }\n}\n\n.place {\n  display: none;\n}\n\n.game {\n  display: none;\n}\n"],"sourceRoot":""}]);
